
.productsHeading {
  margin: 2vmax auto;
  width: 15vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.171);
  padding: 2vmax;
  color: rgba(0, 0, 0, 0.678);
  font: 500 1.5vmax "Roboto";
  text-align: center;
}

.services {
	/* height: 100vh; */
    display: grid;
    place-items: center;
    padding: 100px 0;
}

.services> div {
	/* width: 100vw; */
    display: grid;
    grid-template-columns: repeat(2,minmax(400px,1fr));
    /* grid-template-rows: repeat(2,minmax(250px,1fr)); */
    column-gap: 10px;
    row-gap: 30px;
}

@media (max-width: 1180px){

	

	.services {
		/* height: 100vh; */
		display: grid;
		place-items: center;
		padding: 50px 0;
	}
	.services > div{
        grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
		    row-gap: 50px;
      
    }
}



.paginationBox {
  display: flex;
  justify-content: center;
  margin: 6vmax;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
}

.page-item {
  background-color: rgb(255, 255, 255);
  list-style: none;
  border: 1px solid rgba(0, 0, 0, 0.178);
  padding: 1vmax 1.5vmax;
  transition: all 0.3s;
  cursor: pointer;
}
.page-item:first-child {
  border-radius: 5px 0 0 5px;
}

.page-item:last-child {
  border-radius: 0 5px 5px 0;
}
.page-link {
  text-decoration: none;
  font: 300 0.7vmax "Roboto";
  color: rgb(80, 80, 80);
  transition: all 0.3s;
}

.page-item:hover {
  background-color: rgb(230, 230, 230);
}

.page-item:hover .page-link {
  color: rgb(0, 0, 0);
}

.pageItemActive {
  background-color: tomato;
}

.pageLinkActive {
  color: white;
}

.filter-box {
  width: 10vmax;
  position: absolute;
  top: 10vmax;
  left: 4vmax;
}

.category-box {
  padding: 0%;
}

.category-link {
  list-style: none;
  color: rgba(0, 0, 0, 0.61);
  font: 400 1vmax "Roboto";
  margin: 0.4vmax;
  cursor: pointer;
  transition: all 0.5s;
}
.category-link:hover {
  color: tomato;
}

.filter-box > fieldset {
  border: 1px solid rgba(0, 0, 0, 0.329);
}


@media screen and (max-width: 600px) {
  .filter-box {
    width: 20vmax;
    position: static;
    margin: auto;
  }
  .page-link {
    font: 300 1.7vmax "Roboto";
  }
  .category-link {
    font: 400 2 vmax "Roboto";
  }
}
