.productName {
  padding: 70px 0;
  text-align: center;
  color: red;
  font: 700 1.6vmax "Roboto";
}
.productDetails {
  background-color: rgb(255, 255, 255);
  width: 90vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
  box-shadow: 0 1.4rem 8rem rgba(0, 0, 0, 0.2);
  /* margin: 20px; */
  height: 600px;
  margin: 0 auto;
  margin-top: 50px !important;
  margin-bottom: 50px;
  border-radius: 20px;
}

.productDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly; */
  /* align-items: center; */
  margin-right: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}
.productDetails > div > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.6vmax "Roboto";
}
/* .productDetails > div:last-child {
  align-items: flex-start;
} */
.productDetails > div > p {
  font-size: 1.5vmax;
  color: rgb(117, 116, 116);
  text-align: justify
}
.service-info{
  height: 300px;
  width: 300px;
}
.service-info-2{
  height: 300px;
  width: 600px;
}
.service-info img{
  height: 300px;
  border-radius:20px;
}
.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font-weight: bold;
  size:30px;
}

.detailsBlock-1 > p {
  color: rgb(54, 54, 54);
  font-weight: bold;
  size:20px;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* border-top: 1px solid rgba(0, 0, 0, 0.205); */
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.205); */
  width: 70%;
  /* padding: 1vmax 0; */
}

.detailsBlock-2-span {
  font: 20px;
  color: rgba(0, 0, 0, 0.699);
}


.detailsBlock-3 > p {
  /* border-top: 1px solid rgba(0, 0, 0, 0.205); */
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.205); */
  /* padding: 1vmax 0; */
  size: 30px;
  margin: 1vmax 0;
  font-weight: bold;
  color: black;
  text-align: justify;
}
.detailsBlock-3 > p > span{
  /* border-top: 1px solid rgba(0, 0, 0, 0.205); */
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.205); */
  /* padding: 1vmax 0; */
  size: 30px;
  margin: 1vmax 0;
  color: #808080;
}


.detailsBlock-4 > p {
  /* border-top: 1px solid rgba(0, 0, 0, 0.205); */
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.205); */
  /* padding: 1vmax 0; */
  color: rgb(54, 54, 54);
  font: 200 1.6vmax "Roboto";
  margin: 1vmax 0;
}
.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  /* padding: 0.5vmax; */
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 > button:last-child {
  border: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: tomato;
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.5vmax 2vmax;
  margin: 1vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(214, 84, 61);
}

/* .carouselMain img {
  width: 50%;
} */


.detailsBlock-5{
  display: flex;
 
}

.detailsBlock-5 > div{
  margin-right: 1vmax;
 
}
.submitReview {
  border: none;
  background-color: tomato;
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(197, 68, 45);
  transform: scale(1.1);
}

.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Roboto";
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax "Roboto";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}

.reviews  {
	/* height: 100vh; */
    display: grid;
    place-items: center;
    padding: 100px 0;
}
.reviews  > div {
	/* width: 100vw; */
    display: grid;
    grid-template-columns: repeat(3,minmax(400px,1fr));
    /* grid-template-rows: repeat(2,minmax(250px,1fr)); */
    column-gap: 10px;
    row-gap: 30px;
}


.noReviews {
  font: 400 1.3vmax "Gill Sans";
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
}

@media screen and (max-width: 600px) {
  .productDetails {
    flex-direction: column;
    height: unset;
  }

  .productDetails > div:last-child {
    align-items: center;
  }

  .detailsBlock-1 > h2 {
    font-size: 2.8vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
  }

  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }

  .detailsBlock-3 > h1 {
    font: 700 3vmax "Franklin Gothic Medium";
    text-align: center;
  }

  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax "Roboto";
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax "Roboto";
  }

  .detailsBlock-4 {
    font: 500 2.5vmax sans-serif;
  }

  .detailsBlock-4 > p {
    font: 300 1.8vmax sans-serif;
  }

  .submitReview {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .reviewCard > p {
    font: 600 3vw "Roboto";
  }
  .reviewCardComment {
    font: 300 5vw cursive;
  }
}
.serviceBanner > img {
  width: 100%;
  height: 400px;
}

.service-heading  {
 line-height: 100px;
 background-color: #2E8B57;
 color: white;
 font-size: 2rem;
 text-align: center;
}

.service-details > p {
  font-size: 1.2rem;
  /* font-weight: bold; */
  padding-top: 1.2rem;
}
.service-details > p > span {
  font-size: 1.1rem;
  color : green;
}
.service-main {
  display: grid;
